import { useNotification as useN } from "@kyvg/vue3-notification"

export function useNotification() {
  const route = useRoute()
  const { notify } = useN()

  async function notifySuccess(
    text: string,
    {
      id,
      persistOnRouteChange = true,
      persistOnFirstRouteChange = false,
    }: {
      id?: number | string
      persistOnRouteChange?: boolean
      persistOnFirstRouteChange?: boolean
    } = {}
  ) {
    if (!persistOnRouteChange) {
      let routeChangeCounter = 0
      const unwatch = watch(
        () => route.path,
        () => {
          routeChangeCounter++

          if (!persistOnFirstRouteChange || routeChangeCounter > 1) {
            notify.close(id)
            unwatch()
          }
        }
      )
    }

    notify.close(id)
    await new Promise((resolve) => setTimeout(resolve, 300)) // wait for close animation
    notify.close(id) // close immediately if it was open while waiting

    return notify({
      id: id as number,
      type: "success",
      text: text,
      duration: 3000,
    })
  }

  async function notifyError(
    text: string,
    {
      id,
      persistOnRouteChange = false,
      persistOnFirstRouteChange = false,
    }: {
      id?: number | string
      persistOnRouteChange?: boolean
      persistOnFirstRouteChange?: boolean
    } = {}
  ) {
    if (!persistOnRouteChange) {
      let routeChangeCounter = 0
      const unwatch = watch(
        () => route.path,
        () => {
          routeChangeCounter++

          if (!persistOnFirstRouteChange || routeChangeCounter > 1) {
            notify.close(id)
            unwatch()
          }
        }
      )
    }

    notify.close(id)
    await new Promise((resolve) => setTimeout(resolve, 300)) // wait for close animation
    notify.close(id) // close immediately if it was open while waiting

    return notify({
      id: id as number,
      type: "error",
      text: text,
      duration: -1,
    })
  }

  async function hideNotification(id?: number | string) {
    notify.close(id)
  }

  return {
    notifySuccess,
    notifyError,
    hideNotification,
  }
}
